import requireurl from '../../requist/requist.js'
import Loadings from '../loading/loading.vue'

export default {
	components:{
		Loadings
	},
	data(){
		return{
			changeValue:'',
			password:'',
			LoadingS:true
		}
	},
    onReady: function (e) {
       
    },
	mounted() {
		_that.clearLoding()
	},
	created() {
		
	},
    methods: {
      loginIn(){
		if(this.initWord() === false)return
		var _that = this
		_that.runLoding()
		  wx.login({
			success (res) {
			  if (res.code) {
				  let obj = {
				  	  username : _that.changeValue,
				  	  password : _that.password,
				  	  code : res.code
				    }
				  requireurl.request('u/v1/wx/login/',obj,_that.userlogin,_that.errlogin)
			  } else {
				_that.LoadingS = false
				console.log('登录失败！' + res.errMsg)
			  }
			}
		  })
	  },
	  userlogin(e){
		  console.log(e)
		  this.clearLoding()
		  if(e.statusCode == 201){
			  this.clearLoding()
			  uni.setStorage({
			      key: 'storage_user',
			      data: e.data.token,
			      success: function () {
					  uni.redirectTo({
					      url: '../today/today'
					  });
			      }
			  });
		  }else{
			  wx.showToast({
			    title: e.data.msg,
				icon: 'none'
			  })
		  }
		  
	  },
	 
	 initWord(){
		 if(this.changeValue !== '' && this.password !== ''){
			 return true;
		 }else{
			 return false
		 }
	 },
	 
	  clearLoding(){
		this.LoadingS = false
	  },
	  runLoding(){
		this.LoadingS = true
	  }
    },
	mounted() {
		this.clearLoding()
	}
}
